import {Component} from "@angular/core";
/**
 * Created by skoval on 17-Mar-17.
 */
@Component ({
  selector: 'click-me',
  template: `
    <button (click)="onClick()">Click me!</button>
    {{clickMessage}}
  `
})
export class ClickMeComponent {
  clickMessage = '';

  onClick() {
    this.clickMessage = 'You are my hero';
  }
}



