import {Component} from "@angular/core";
/**
 * Created by skoval on 18-Mar-17.
 */
@Component({
  selector: 'keyup',
  template: `
    <input #box (keyup.enter)="onEnter(box.value)" (blur)="onEnter(box.value)">
    <p>{{values}}</p>
  `
})
export class KeyUpComponent_v1 {
  values = '';

  onKey(value: string) {
    this.values += value + ' | ';
  }

  onEnter(value: string) {
    this.values = value;
  }
}



